
import { Link } from 'react-router-dom';
import { useState } from 'react';
import { ResumeSteps } from "../component/containers/ResumeSteps";

import Slider from 'rc-slider';

//CSS
import 'react-rangeslider/lib/index.css'
import '../assets/css/pages/create-resume.css'

function AddSkills() {

    const [value, setValue] = useState(50);

    const handleOnChangeRange = (values) => {
        setValue(values)
    }

    return (
        <div className="main-content">
            <section className="personal-information">
                <div className="container">
                    <div className="section-title varient-two">
                        <h4>Create Resume</h4>
                    </div>
                    <ResumeSteps completedSteps={3} />

                    <div className="create-resume-step-content light-blue-bg">
                        <div className="resume-header">
                            <Link to="/createResume/ProfessionalDetails" className='back-btn'><i className="bi bi-arrow-left"></i></Link>
                            <h4>Add Your Skills</h4>
                        </div>
                        <div className="resume-body">
                            <div className='create-resume-form'>
                                <div className="row">
                                    <div className="col-sm-6">
                                        <div className="form-group">
                                            <label htmlFor="degree" className="form-label">Add Your Skills</label>
                                            <input type="text" className="form-control" id="skills" placeholder="Enter your skills" />
                                        </div>
                                    </div>
                                </div>
                                <div className='row'>
                                    <div className='col-xl-3 col-lg-4 col-sm-6'>
                                        <button className='button button-blue w-100'>Add</button>
                                    </div>
                                    <div className='col-xl-3 col-lg-4 col-sm-6'>
                                        <button className='button button-light-blue w-100'>Submit</button>
                                    </div>
                                </div>
                                <div className='row mt-5'>
                                    <div className='col-md-12'>
                                        <div className='added-qualification-list'>
                                            <h4 className='block-title'>My Skills</h4>
                                            <Slider
                                                value={value}
                                                orientation="horizontal"
                                                onChange={handleOnChangeRange}
                                            />
                                        </div>
                                    </div>
                                </div>
                                <button className='text-btn save-exit-btn'>Save and Exit</button>
                            </div>
                        </div>
                    </div>

                </div>
            </section>
        </div>
    )
}

export default AddSkills;